@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

html, body, #root {
    height: 100%;
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
}

@-webkit-keyframes grow-width {
    0% {
        max-width: 0;
        margin: 0;
        padding: 0;
    }
}

@keyframes grow-width {
    0% {
        max-width: 0;
        margin: 0;
        padding: 0;
    }
}

@-webkit-keyframes grow-height {
    0% {
        max-height: 0;
        margin: 0;
        padding: 0;
    }
}

@keyframes grow-height {
    0% {
        max-height: 0;
        margin: 0;
        padding: 0;
    }
}

.Bank{display:flex;align-items:center;flex-direction:column;font-size:32px;text-align:center;padding:100px;-webkit-animation:fade-in 1s;animation:fade-in 1s}.word-bank{resize:none;font-size:24px;width:50vw;height:35vh;background-color:transparent;border-radius:10px;border:solid 1px grey;margin:20px;padding:20px}.Bank p{margin:5px;width:100%}

.commands{font-size:24px;position:fixed;text-align:left;caret-color:transparent;background-color:transparent;margin:10px;padding:10px;width:calc(100vw - 40px);bottom:0;border:none;outline:none}

.NavButton{background-color:transparent;border:solid 1px grey;font-size:24px;text-align:center;border-radius:10px;cursor:pointer;padding:10px;margin:10px;width:280px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}

.Button{background-color:transparent;border:solid 1px grey;font-size:18px;text-align:center;border-radius:10px;cursor:pointer;margin:5px;width:200px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}

.Input{font-size:24px;width:100%;min-width:250px;text-align:center;background-color:transparent;border:solid 1px grey;border-radius:10px;box-sizing:border-box}.Input[type=number]::-webkit-inner-spin-button,.Input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.Input[type=number]{-moz-appearance:textfield;-webkit-appearance:textfield;appearance:textfield}

.Menu{width:100%}.menu-button{font-size:24px;width:100%;min-width:250px;text-align:center;background-color:transparent;border:solid 1px grey;border-radius:10px;box-sizing:border-box}.menu-options{visibility:hidden;position:absolute;background-color:rgba(0,0,0,0.8);border:solid 1px grey;border-radius:10px;text-align:left;z-index:1}.menu-option{margin:5px;cursor:pointer}.Menu:hover .menu-options{visibility:visible}

.Toggle{font-size:24px;width:100%;text-align:center;background-color:transparent;border:solid 1px grey;border-radius:10px;box-sizing:border-box;min-width:250px;cursor:pointer}

.Settings{display:flex;min-width:100%;min-height:100%;align-items:center;flex-direction:column;font-size:32px}.toggle-button{background-color:transparent;border:solid 1px grey;font-size:24px;text-align:center;border-radius:10px;cursor:pointer;padding:10px;margin:10px;width:280px}.theme-settings,.type-settings{display:flex;flex-direction:column;align-items:center;text-align:center;padding:50px;margin:50px;width:75%;-webkit-animation:fadeIn 1s;animation:fadeIn 1s}.subsection{display:flex;flex-direction:column;align-items:center;margin:50px}.setting-container{display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center}.setting{font-size:24px;text-align:center;padding:0px;margin:25px}.setting p{margin-top:5px;margin-bottom:5px;width:100%}@-webkit-keyframes fadeIn{0%{opacity:0}}@keyframes fadeIn{0%{opacity:0}}

.Display{font-size:36px;width:5vw;text-align:center}

.Graph{position:relative;width:clamp(300px, 40vw, 700px)}

.Timer{font-size:36px;width:5vw}

.Words{font-size:clamp(15px, 2.7vw, 24px);text-align:left;width:clamp(300px, 55vw, 800px);min-width:300px;max-height:90vh;overflow:auto;scrollbar-width:none;-ms-overflow-style:none}.Words::-webkit-scrollbar{width:0;height:0}.Line{-webkit-animation:grow-height 0.5s, fade-in 0.5s;animation:grow-height 0.5s, fade-in 0.5s;max-width:-webkit-max-content;max-width:-moz-max-content;max-width:max-content;border-radius:3px;max-height:45px;transition:0.5s, background-color 0s;overflow:hidden;position:relative}.Line.inactive{opacity:0;max-height:0px;margin:0px;transition:0.5s}.Char{overflow:hidden;transition:color 0.2s;border-radius:3px}.Word{border-radius:3px}

.Speed{font-size:36px;text-align:right;width:5vw}

.Type{display:flex;min-width:100%;min-height:100%;justify-content:center;outline:none}.Words{-webkit-animation:fade-in 1s;animation:fade-in 1s;margin:20px}.info{display:flex;width:95vw;justify-content:space-between;-webkit-animation:fade-in 1s;animation:fade-in 1s}.main{position:absolute;display:flex;flex-wrap:wrap;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);justify-content:space-around;align-items:center}.results{margin:20px;font-size:clamp(15px, 2.7vw, 24px)}.results p{max-height:45px;-webkit-animation:fade-in 0.5s, grow-height 0.5s;animation:fade-in 0.5s, grow-height 0.5s;overflow:hidden}.results span{transition:color 0.5s}

